.banner{
    color: white;
    object-fit: contain;
    height: 650px;
}

.banner__contents{
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}

.banner__title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.banner__description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    max-width: 555px;
    height: 80px;
    font-size: 1.3rem;
}

.banner__button{
    cursor: pointer;
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: .2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    background-color: rgba(51, 51, 51, .5);
    padding-bottom: 0.5rem;
}

.banner__button:hover{
    color: #000;
    background-color: #e6e6e6;
    transition: all .3s;
}

.banner--fadeBottom{
    height: 20.4rem;
    background-image: linear-gradient(180deg, transparent, rgba(37, 37, 37, .61), #111);
}